@use '../../variables.sass' as v

.contacts
  text-align: center
  .contacts__link
    position: relative
    display: inline-block
    align-self: center
    padding: 16px 40px 16px 70px
    color: #000
    background-color: v.$color-btn
    &::before
      content: ''
      position: absolute
      left: 10%
      top: 15px
      width: 30px
      height: 30px
      background-image: url("data:image/png;base64,")
      background-size: cover
    &:hover
      box-shadow: inset 20em 0 0 0 lighten(v.$color-bright, 10%)
